.about {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 100px;

  &__wrap {
    filter: drop-shadow(0 -14px 40px rgba($mc, .9));
  }

  &__text {
    max-width: 600px;
    width: 100%;
    background-color: $mc;
    color: $wc;
    clip-path: polygon(5% 0, 90% 0, 100% 70%, 0 100%);
    padding: 50px 70px 50px;

    span {
      color: $sc;
      margin-bottom: 10px;
      display: block;
      text-transform: uppercase;
    }

    p {
      color: $wc;
      font-size: 18px;
      margin-bottom: 15px;
      line-height: 1.4;
    }
  }
}