.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 99999;

  &:after {
    position: absolute;
    content: '';
    width: 100%;
    height: 100%;
    top: -100%;
    background-color: rgba($mc, 0.9);
    transition: $tr;
    z-index: -1;
  }

  &.fixed {
    transition: $tr;

    &:after {
      top: 0;
    }

    .fixed_nav {
      display: block;

      li {
        margin-right: 30px;
      }
    }

    .header__col {
      &:first-child {
        width: auto;

        .header__nav {
          display: none;
        }
      }
    }
  }

  &__col {
    &:first-child {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      max-width: calc(var(--navWidth) - 7%);
      width: 100%;
    }
  }

  .fixed_nav {
    display: none;
  }

  &__nav {
    ul {
      li {
        display: inline-block;
        margin-right: 10px;

        &:last-child {
          margin-right: 0;
        }

        a {
          color: $wc;

          &:hover {
            color: $sc;
          }
        }
      }
    }
  }

  &__wrap {
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: space-between;
    padding: 15px 30px;
  }

  .logo {
    display: flex;
    align-items: center;
    justify-content: center;

    &__img-wrap {
      width: 100%;
      max-width: 120px;
    }

    a {
      display: flex;
      flex-direction: column;
    }

    span {
      white-space: nowrap;
      margin-top: 4px;
      color: $wc;
    }
  }
}