.plan {
  .tabs {
    display: table;
    position: relative;
    overflow: hidden;
    margin: 0;
    width: 100%;
    text-align: center;

    li {
      display: inline-block;
      line-height: 38px;
      padding: 0;
      position: relative;
    }

    a {
      color: $mc;
      font-weight: 500;
      display: block;
      letter-spacing: 0;
      outline: none;
      padding: 0 15px;
      text-decoration: none;
      transition: all 0.2s ease-in-out;

      &:after {
        position: absolute;
        content: '';
        clip-path: polygon(10px 0, 100% 0, 91% 70%, 0 100%);
        width: 80%;
        height: 100%;
        background-color: $sc;
        left: 0;
        top: 20%;
        z-index: -1;
        display: none;
      }
    }
  }

  .tab_content {
    width: 70%;
    margin: auto;
  }

  .tabs_item {
    display: none;
    padding: 30px 0;
    text-align: center;

    &:first-child {
      display: block;
    }

    .btn {
      background-color: $wc;
      color: $mc;

      &:hover {
        background-color: $sc;
        color: $wc;
      }
    }
  }

  .current a {
    color: $mc;
    position: relative;
    background: transparent;
    z-index: 1;

    &:after {
      display: block;
    }
  }
}