.advantages {
  &__item {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 100px;

    &:last-child {
      margin-bottom: 0;
    }

    &:nth-child(2n) {
      flex-direction: row-reverse;
    }
  }

  &__desc {
    max-width: 45%;
    width: 100%;

    p {
      line-height: 1.5;
      text-align: justify;
    }
  }

  &__img {
    max-width: 45%;
    width: 100%;
    position: relative;

    &:before,
    &:after {
      position: absolute;
      content: '';
    }

    &:before {
      height: 80%;
      width: 0;
      height: 0;
      border-bottom: 300px solid $mc;
      border-right: 300px solid transparent;
      bottom: -7%;
      left: -7%;
      z-index: -1;
    }

    &:after {
      height: 90%;
      width: 63%;
      background-image: url('../../assets/img/bg_elem.svg');
      background-repeat: no-repeat;
      bottom: -7%;
      right: -7%;
      background-size: cover;
      z-index: -2;
    }
  }

}