.main_slider {
  height: 100vh;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: $mc;

  &__text-side {
    width: 100%;
    height: 100%;
    margin-right: -200px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: $wc;
  }

  &__title {
    filter: drop-shadow(0 -14px 40px rgba(#fbe115c2, .4));

    h1 {
      padding: 20px 70px 30px 15px;
      background-color: #fbe115c2;
      clip-path: polygon(10px 0, 100% 0, 91% 70%, 0 100%);
      display: block;
      margin: 0;
    }
  }

  &__subtitle {
    margin-bottom: 40px;
  }

  &__desc {
    max-width: 450px;
    font-weight: 500;
    background-color: $wc;
    color: $bc;
    padding: 40px 20px 30px 80px;
    clip-path: polygon(10% 20%, 100% 0, 91% 100%, 0 91%);
  }

  &__slider {
    max-width: 55%;
    width: 100%;
    height: 100%;
    clip-path: polygon(150px 0, 100% 0, 100% 100%, 150px 100%, 0 65%);
    background-position: center;

    * {
      height: 100%;
      width: 100%;
    }

    .slide {
      background-size: cover;
      animation: leaves 15s ease-in-out infinite alternate;
      position: relative;

      &:after {
        position: absolute;
        content: '';
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(#1c2c40, 0.3);
      }
      overflow: hidden;

      @keyframes leaves {
        0% {
          transform: scale(1);
        }

        100% {
          transform: scale(1.2);
        }
      }

    }
  }
}